import { Fragment } from "react";
import { Box, IconButton } from "@mui/material";
import { useTheme } from "@mui/material/styles";

import { useStore } from "@/store/useStore.jsx";

import LogoIcon from "@mui/icons-material/Foundation";
import HelpIcon from "@mui/icons-material/QuestionMark";
import SettingsIcon from "@mui/icons-material/Settings";

export default function Banner({ page }) {
    const theme = useTheme();
    const setNextColor = useStore((state) => state.setNextColor);

    const btns = [
        { icon: LogoIcon, action: () => console.log("Logo clicked") },
        {
            icon: HelpIcon,
            action: () => useStore.setState({ page: "profiles" }),
        },
        {
            icon: SettingsIcon,
            action: () => useStore.setState({ page: "map" }),
        },
    ];

    return (
        <Box
            sx={{
                width: "100%",
                height: `${theme.grid.units.h}px`,
                marginY: theme.grid.spacing,
                borderRadius: theme.brdRad,
                // border: `solid 2px ${theme.palette.primary.main}`,
                display: "flex",
                flexDirection: "row",
                justifyContent: "flex-start",
                alignItems: "center",
            }}
        >
            {btns.map((btn, index) => (
                <Fragment key={index}>
                    <IconButton
                        onClick={btn.action}
                        sx={{
                            width: `${theme.iconH}`,
                            height: `${theme.iconH}`,
                            marginLeft: index === 0 ? 0 : theme.grid.spacing,
                        }}
                    >
                        <btn.icon
                            sx={{
                                width: "60%",
                                height: "60%",
                                color: theme.palette.secondary.main,
                            }}
                        />
                    </IconButton>
                    {index == 0 ? <Box sx={{ flexGrow: 1 }}></Box> : null}
                </Fragment>
            ))}
        </Box>
    );
}
