import { useState, useMemo } from "react";
import {
    Autocomplete,
    TextField,
    IconButton,
    Typography,
    createFilterOptions,
} from "@mui/material";
import ShadowBox from "./ShadowBox.jsx";
import { useStore } from "@/store/useStore.jsx";

export default function AddressInputAuto({
    theme,
    label = "Da",
    placeholder = "Seleziona una Destinazione",
}) {
    const { addresses, setMarkers } = useStore();
    const [value, setValue] = useState(null);
    const [inputValue, setInputValue] = useState("");

    const options = useMemo(
        () =>
            addresses.map((feature, idx) => ({
                label: `${feature.properties["nome strada"]}, ${feature.properties.numero}`,
                value: feature.geometry.coordinates,
            })),
        [addresses]
    );

    const filterOptions = createFilterOptions({
        limit: 10,
        ignoreCase: true,
        trim: true,
    });

    return (
        <ShadowBox
            outlined={false}
            sx={{
                height: `${theme.grid.units.h}px`,
                marginY: theme.grid.spacing,
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
            }}
        >
            <IconButton disabled>
                <Typography>{label}</Typography>
            </IconButton>

            <ShadowBox
                // focus
                sx={{
                    height: "100%",
                    marginLeft: theme.grid.spacing,
                    borderRadius: theme.brdRad,
                    flexGrow: 1,
                    display: "flex",
                    alignItems: "center",
                    backgroundColor: theme.palette.primary.secondary,
                    paddingLeft: theme.offRad,
                }}
            >
                <Autocomplete
                    value={value}
                    disableClearable
                    onChange={(e, newValue) => {
                        setValue(newValue);

                        setMarkers({
                            coordinates: newValue.value.reverse(),
                            key: Date.now(),
                        });
                    }}
                    inputValue={inputValue}
                    onInputChange={(e, newInputValue) => {
                        setInputValue(newInputValue);
                    }}
                    options={options}
                    filterOptions={filterOptions}
                    getOptionLabel={(opt) => opt?.label || ""}
                    open={inputValue.length > 0}
                    sx={{
                        width: "100%",
                        "& .MuiOutlinedInput-root": {
                            paddingY: 0,
                            paddingX: 0.5,
                            height: "100%",
                            color: theme.palette.primary.main,
                        },
                        "& .MuiInputBase-input": {
                            padding: 0,
                            fontSize: "0.9rem",
                        },
                        "& .MuiOutlinedInput-notchedOutline": {
                            border: "none",
                        },
                        "& .MuiAutocomplete-endAdornment": {
                            display: "none",
                        },
                    }}
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            placeholder={placeholder}
                            variant="outlined"
                        />
                    )}
                />
            </ShadowBox>
        </ShadowBox>
    );
}
